
.start {
  opacity: 0;
}

.end {
  opacity: 1;
  transition: opacity 0.5s;
}

.main-bg {
  height: 300px;
  background-size: cover;
  background-position: center;
}

.navbar {
  padding: 10px 0;
  background-color: #fff !important;
}

.brand {
  color: #333 !important;
}

.navLink {
  color: #777 !important;
  text-decoration: none;
  margin-right: 1em;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button.btn {
  display: block;
  margin: 20px auto; 
}

.btn-btc {
  margin-top: 50px;
  font-size: 12px;
  color: #333;
  text-decoration: underline;
  display: inline-flex;
  align-items: center;
  justify-content: center; 
}

.theButtonArea{
  margin-bottom: 100px;
}
.theButton{
  background: none;
  border: none;
  border-bottom: 0.5px solid #777;
}

.image-container {
  width: 100%; 
  height: 300px; 
  overflow: hidden; 
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: contain; 
}

@media (max-width: 768px) {
  .col-md-4 {
    width: 50%; 
  }

  .image-container {
    height: 250px;
  }
}

@media (max-width: 576px) {
  .col-md-4 {
    width: 100%; 
  }

  .image-container {
    height: 200px; 
  }
}

.tabArea{
  margin-top: 300px;
  text-align: center;
}
.detailTitle{
  font-size: 1.5em;

}

@media (max-width: 768px) {
  .App-header {
    font-size: calc(8px + 2vmin);
  }

  .btn-btc {
    font-size: 10px;
  }
}